import * as PIXI from 'pixi.js'
import { pixelate } from './pixelate'
import { Moon } from './Moon'
import { ship } from './ship'

const size = 50

class View {
    start() {
        this.renderer = new PIXI.Renderer({
            view: document.querySelector('.view'),
            resolution: window.devicePixelRatio,
            transparent: true,
            antialias: true,
        })
        this.stage = new PIXI.Container()
        this.stage.addChild(pixelate)
        this.stage.addChild(ship)
        this.resize()
        this.moon = new Moon()
        this.update()
    }

    get size() {
        return size
    }

    resize() {
        if (window.innerWidth > window.innerHeight) {
            console.log('not done yet')
            // pixelate.scale.set(window.innerWidth / size)
            // this.height = window.innerWidth / window.innerHeight / this.width
        } else {
            this.stage.scale.set(window.innerHeight / size)
            // pixelate.position.set(0, (window.innerHeight / pixelate.scale.x - size) / 2)
        }
        this.renderer.resize(window.innerWidth, window.innerHeight)
    }

    update() {
        this.moon.update()
        ship.update()
        this.renderer.render(this.stage)
        requestAnimationFrame(() => this.update())
    }
}

export const view = new View()